body {
  min-width: 320px;
  font-size: 15px;
  line-height: 17px;
  font-family: "Fira Sans", "Arial", sans-serif;
  color: #484848;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.first-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  max-width: 1366px;
  min-height: 906px;
  margin: 0 auto;
  background-image: url("../img/bg_girl_desktop@x1.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .first-wrapper {
      background-image: url("../img/bg_girl_desktop@x2.png"); } }
  @media (max-width: 767px) {
    .first-wrapper {
      background-image: url("../img/bg_girl_mobile@x1.png"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .first-wrapper {
      background-image: url("../img/bg_girl_mobile@x2.png"); } }
  @media (min-width: 320px) and (max-width: 767px) {
    .first-wrapper {
      min-height: 632px;
      padding-top: 20px;
      background-size: 100%; } }

.page-header {
  position: absolute;
  top: 32px;
  left: 50%;
  width: 83.6017%;
  width: 1142px;
  margin-bottom: 433px;
  transform: translateX(-50%); }
  @media (max-width: 1142px) {
    .page-header {
      width: 100%;
      padding-left: 17px;
      padding-right: 17px; } }
  @media (min-width: 320px) and (max-width: 420px) {
    .page-header {
      padding: 0; } }

.page-header__list {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none; }
  @media (min-width: 320px) and (max-width: 420px) {
    .page-header__list {
      flex-direction: column;
      align-items: flex-end; } }

.page-header__item {
  margin: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px; }
  @media (max-width: 767px) {
    .page-header__item {
      font-size: 14px;
      line-height: 17px; } }
  .page-header__item--phone {
    margin-right: 31.7863%; }
    @media (max-width: 1142px) {
      .page-header__item--phone {
        margin-left: 0;
        margin-right: 0; } }
    @media (min-width: 320px) and (max-width: 420px) {
      .page-header__item--phone {
        margin-left: auto;
        margin-right: 7px; } }
  .page-header__item--logo {
    margin-right: 33.8003%;
    width: 137px; }
    .page-header__item--logo img {
      width: 137px;
      height: 48px; }
    @media (max-width: 1142px) {
      .page-header__item--logo {
        margin-left: auto;
        margin-right: auto; } }
    @media (max-width: 767px) {
      .page-header__item--logo img {
        width: 120px;
        height: 42px; } }
    @media (min-width: 320px) and (max-width: 420px) {
      .page-header__item--logo {
        position: absolute;
        top: 50%;
        left: 16px;
        margin-right: 0;
        transform: translateY(-50%); } }
  .page-header__item--order-call {
    margin-right: 0; }

.page-header__btn {
  padding: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #484848;
  font-variant: small-caps;
  background-color: transparent;
  border: none;
  border-bottom: 1px dashed #484848;
  cursor: pointer; }
  @media (max-width: 767px) {
    .page-header__btn {
      font-size: 14px;
      line-height: 17px; } }
  @media (min-width: 320px) and (max-width: 420px) {
    .page-header__btn {
      margin-top: 8px;
      margin-right: 9px; } }

.first-promo {
  width: 343px;
  margin-bottom: 32px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }
  @media (max-width: 767px) {
    .first-promo {
      width: 288px;
      font-size: 20px;
      line-height: 24px; } }

.help-scroll {
  margin-bottom: 140px; }

.help-scroll__img {
  width: 76px;
  height: 132px; }
